import React, {Component} from 'react'

class List extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedObj: '',
      toggleCls: true
    };
  }
  componentWillReceiveProps = nextProps => {
    if(nextProps.users.length > 0){
      this.setState({
        toggleCls: true
      });
    }
  }
  clickUser = (val,id) => {
    this.setState({
      selectedObj: this.props.users[id],
      toggleCls: false
    })
  }

  setObj = Obj => {
    return (
      <div>
        <img alt='no' src={Obj.avatar_url} />
        <h3>{Obj.login}</h3>
        <a href={Obj.html_url} target='_blank'>click here for profile </a>
      </div>
    )
  }

  highlight = name => {
    var html = name
      .toLowerCase()
      .replace(this.props.textInput.value.toLowerCase().trim(), "<span style='color:blue'>" + this.props.textInput.value + "</span>");
    return <a
      style={{
      'color': 'black'
    }}
      dangerouslySetInnerHTML={{
      __html: html
    }}/>
  }

  render() {
    return (
      <div>
        <div
          id="myDropdown"
          className={this.state.toggleCls
          ? 'dropdown-content show'
          : 'dropdown-content'}>
          {this.props.users.length > 0 && 
            this.props.users.map((val, idx) => {
              return <div key={idx} onClick={() => this.clickUser(val, idx)}>{this.highlight(val.login)}</div>
            })
          }
        </div>
        {this.state.selectedObj && this.setObj(this.state.selectedObj)
}
      </div>
    )
  }
}

export default List;


