export default function images(state = { dropdownArr:[], hideBox: false }, action) {
    switch (action.type) {
      case 'USERS_RECIEVED':
            return {...state, dropdownArr: action.users, hideBox: true}
      case 'LOAD_USERS':
            return {...state, dropdownArr: action}
      case 'HIDE_USERS':
            return {...state, hideBox: action.payload}
      default:
        return state
    }
  }


