import React, { Component } from 'react'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import * as actions from "../actions"
import LoadingBar from 'react-redux-loading-bar'
import List from './List'

class App extends Component {

    onChange = e => {
      if (e.target.value.length > 6) {
        const apiEndpoint = `https://api.github.com/search/users?q=${e.target.value}`;
        this.props.loadUsers(apiEndpoint);        
      }else{
        this.props.hideUsers();
      }
    }
  
    componentDidMount = () => {
      this.textInput.focus();
    }

    render() {
      return (
        <div>
          <header>
            <LoadingBar/>
          </header>
          <input type='search' style={{ width: '300px' }} ref={(input) => { this.textInput = input; }} placeholder='user name should be greater than 4 characters' onChange={this.onChange} />         
          {this.props.hideBox ?
          <List users={this.props.dropdownArr} hideBox={this.props.hideBox} textInput={this.textInput}></List>:
          <h3>{`No  ${this.textInput ? ((this.textInput.value === "") ? "" : this.textInput.value):""} user found on Github`} </h3>
          }
        </div>
      );
    }
  }

  export default connect(
    state => ({dropdownArr: state.appReducer.dropdownArr, hideBox: state.appReducer.hideBox }),
    dispatch => bindActionCreators(actions, dispatch)
  )(App)


